import { Injectable, Output } from '@angular/core';
import { Reserva } from '../interfaces/reserva';
import { HorarioTurnos } from '../interfaces/horario-turnos';
import { RequestService } from './request.service';
import 'rxjs/add/operator/map';

@Injectable()
export class ReservaService {
  public horas_disponibles:HorarioTurnos[];
  public datos_reserva:Reserva;


  constructor(private request:RequestService) { 
    this.datos_reserva = new Reserva();
  }
  
  /** enviarReserva
   * Envía los datos de la reserva para generar un nuevo turno
   */
  public enviarReserva(captcha: string){
    
    return this.request.post("reservas.realizar_reserva.php",{...this.datos_reserva, captcha}).map(
      cod_reserva => {
        this.datos_reserva.codigo_reserva = cod_reserva as string;
        return cod_reserva;
      }
    )
  }

  /** consultarTurno
   * Consulta y trae la información de un turno según el código
   * @param codigo codigo de turno
   *///this.codigo,this.matricula,this.cliente.numero_documento
  public consultarTurno(codigo:string,matricula:string,nrodocumento:string){
    let dato = { codigo: codigo,
      matricula: matricula,
      nrodocumento: nrodocumento };
    return this.request.get("reservas.consulta_codigo.php",dato);
  }

  /** cancelarTurno
   * Envía el número de id de un turno para marcarlo como cancelado en la tabla
   * @param numero ID De tabla rerva
   */
  public cancelarTurno(numero:string, fecha, hora, centro){
    let dato = { 
      numero: numero,
      fecha: fecha,
      hora: hora,
      centro: centro
    };
    return this.request.post("reservas.eliminar_reserva.php",dato);
  }

  
}



