import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  @Input() imagenFondo:string;
  @Input() porcentajeAltura:number;
  estilo_fondo:string;
  public ventana:any;

  // constructor() { }
  constructor(private router: Router) { }

  ngOnInit() {
    this.ventana = this.Ventana();
    this.estilo_fondo = this.getStyleFondo();

    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo({top: 0, behavior: 'smooth'})
  });
  }

  getStyleFondo(){
    let estilos:string = "";
    let alto:number = Math.round( ( this.ventana.alto - document.querySelector('header').scrollHeight ) * this.porcentajeAltura );
    estilos += "height: "+ alto +"px;";
    //estilos += "background-image: url('assets/img/"+this.imagenFondo+"');";
    //estilos += "background-position: center;";
    estilos += "min-height: 320px;";
    estilos += "position:relative;";
    //estilos += "background-size: cover;";
    return estilos;
  }

  Ventana(){
    let ancho;
    let alto;
    if (!window.innerHeight){
      ancho = (document.body.clientWidth);
      alto = (document.body.clientHeight);
    }
    else{
      ancho = (window.innerWidth);
      alto = (window.innerHeight);
    }
    return {
      ancho:ancho,
      alto:alto
    };
  }

}



