import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { Reserva } from '../../../../../interfaces/reserva';

@Component({
  selector: 'app-seleccionar-pago',
  templateUrl: './seleccionar-pago.component.html',
  styleUrls: ['./seleccionar-pago.component.css']
})
export class SeleccionarPagoComponent implements OnInit {

  @Input() datosReserva:Reserva;
  @Output() pasoAtras:EventEmitter<boolean> = new EventEmitter();
  @Output() siguiente:EventEmitter<boolean> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

}



