export class Beneficio{
    id:string
    localidad:string
    comercio:Comercio
    direccion_beneficio:string
    tipo:string
    titulo:string
    porcentaje:string
    descripcion_porcentaje:string
    detalles:any
    validez:string

    constructor(){
      this.comercio = new Comercio();
    }
  }
  
  export class Comercio{
    nombre:string
    logo:string
    propietario:string
    direccion:string
    instagram:string
    facebook:string
    twitter:string
    web:string
  }


