import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Planta } from '../../../../../interfaces/planta';

@Component({
  selector: 'app-selector-de-planta',
  templateUrl: './selector-de-planta.component.html',
  styleUrls: ['./selector-de-planta.component.css']
})
export class SelectorDePlantaComponent {

  @Input() plantas:Planta[];
  @Output() plantaSeleccionada:EventEmitter<Planta> ;

  constructor() { 
    this.plantaSeleccionada = new EventEmitter()
  }

}



