import { Component, OnInit } from '@angular/core';
import { Dropdown } from 'primeng/dropdown';
import { MessageService } from 'primeng/components/common/messageservice';


import { PlantasService } from '../../../servicios/plantas.service';
import { ReservaService } from '../../../servicios/reserva.service';
import { ValidacionesService } from '../../../servicios/validaciones.service';

import { Planta } from '../../../interfaces/planta';
import { Vehiculo,Cliente, Turno, Reserva } from '../../../interfaces/reserva';
import { MensajeError } from '../../../interfaces/mensaje';
import { RequestService } from '../../../servicios/request.service';

import { CaptchaService } from '../../../servicios/captcha.service';

@Component({
  selector: 'app-reservas',
  templateUrl: './reservas.component.html',
  styleUrls: ['./reservas.component.css']
})
export class ReservasComponent implements OnInit {

  public nro_slide = 0; // arranca en cero
  private cantidad_slides = 6;
  public mensajes:Array<MensajeError> = [];
  public terminos_y_condiciones = false;
  public preloader = false;
  public codigo_reserva = ""; 
  public numero_reserva = "";


  public dropdown:Dropdown


  constructor(public plantasService:PlantasService,
              public reserva:ReservaService,
              public validaciones:ValidacionesService,
              private captchaService: CaptchaService
              ) {
              
              }

  ngOnInit() { }

  mostrarErrores(errores_array:MensajeError[]){
    errores_array.forEach(error => {
      this.mensajes.push(error);
    },this);

  }


  mostrarSlide(slide:number){
    //console.log(JSON.stringify(this.reserva.datos_reserva));
    if( slide <= this.cantidad_slides && slide >= 0 ){
      this.nro_slide = slide;
    }
  }

  seleccionarPlanta(planta:Planta){    
    if(planta.id.trim() == '11'){
      this.reserva.datos_reserva.turno.planta = planta;
      this.mostrarSlide(this.nro_slide + 1);
    }
    else if (planta.id.trim() == '12') { 
      window.location.href = 'https://portovial.gob.ec/sitio/index.php/servicio-en-linea/solicitud-orden-de-pago'
    }
    else if (planta.id.trim() == '13') { 
      window.location.href = 'https://www.movilidadmachala.gob.ec/web/turno-online/'
    }
    else if (planta.id.trim() == '14') { 
      window.location.href = 'https://servicios.axiscloud.ec/SVT/paginas/portal/svf_solicitar_servicio.jsp?ps_param_tip_serv=MAT&ps_id_empresa=07'
    }

  }

  seleccionarVehiculo(vehiculo:Vehiculo){
    this.reserva.datos_reserva.vehiculo = vehiculo;
    this.mostrarSlide(this.nro_slide + 1);
  }

  seleccionarCliente(cliente:Cliente){
    this.reserva.datos_reserva.cliente = cliente;
    //console.log(this.reserva)
    this.mostrarSlide(this.nro_slide + 1);
  }

  tomarTurno(turno:Turno){
    this.reserva.datos_reserva.turno = turno;
    //console.log(this.reserva)    
    this.mostrarSlide(this.nro_slide + 1);
  }

  confirmarDatos(){
    this.reservar(this.nro_slide + 1)
    /*
    // Queda inactivo el slide de seleccion de medio de pago
    if(this.reserva.datos_reserva.tarifa.monto  == 0){
      this.reservar(this.nro_slide + 1)
    }else{
      this.mostrarSlide(this.nro_slide + 1);
    }
    */
  }

  reservar(dato){
    this.preloader = true;
    this.reserva.enviarReserva(this.captchaService.getCaptchaToken()).subscribe(
      (reserva:any) => {
        this.codigo_reserva = reserva.codigo;
        this.numero_reserva = reserva.numero;
        this.mostrarSlide(dato);
        this.preloader = false;
        this.reserva.datos_reserva = new Reserva();
      },
      (error)=>{
        let mensaje:MensajeError = {
          severity:"danger",
          summary:"Error al reservar",
          detail:"Por favor vuelva a intentarlo luego"
        }
        this.preloader = false;
        this.mensajes.push(mensaje);
        console.log(error);
      }
    );
    //this.http.post2().subscribe(exito => {console.log("EXITO!", exito); this.preloader = false}, error => { console.log("ERROR", error); this.preloader = false } );
  }
}



