import { Component, OnInit } from '@angular/core';
import { PlantasService } from '../../../servicios/plantas.service';
import { Mensaje, MensajeError } from '../../../interfaces/mensaje';
import { ValidacionesService } from '../../../servicios/validaciones.service';
import { ContactoService } from '../../../servicios/contacto.service';

@Component({
  selector: 'app-contacto',
  templateUrl: './contacto.component.html',
  styleUrls: ['./contacto.component.css']
})
export class ContactoComponent implements OnInit {
  public map_center = { lat:-2.189771 , lng:-79.815410 }
  public preloader = false;
  public mensaje:Mensaje = new Mensaje();
  public codigo_contacto:string = "";
  public confirmarMensaje:boolean = false;
  public mensajesError:MensajeError[] = [];

  constructor(
    public servicioPlantas:PlantasService, 
    private validaciones:ValidacionesService, 
    private servicioContacto:ContactoService) { }

  ngOnInit() {
    //this.centrarMapa();
  }

  centrarMapa(){
    this.servicioPlantas.plantas.map((planta,indice,array:any[])=>{
      this.map_center.lat += planta.lat;
      this.map_center.lng += planta.lng;
      let divisor = ( indice > 0 ) ? 2 : 1 ;
      this.map_center.lat = ((this.map_center.lat) / divisor);
      this.map_center.lng = ((this.map_center.lng) / divisor);
    },this);
  }

  enviarMensaje(){

    this.mensajesError = [];
    if(this.mensaje.nombre == "" || this.mensaje.nombre == undefined ){
      this.mensajesError.push({severity: "danger", summary: "Nombre incorrecto", detail:"Por favor ingrese su nombre y apellido"});
      return;
    }
    if(!this.validaciones.validarMail(this.mensaje.mail)){
      this.mensajesError.push({severity: "danger", summary: "Mail incorrecto", detail:"Por favor ingrese un mail válido"});
      return;
    }
    if(this.mensaje.telefono == "" || this.mensaje.telefono == undefined ){
      this.mensajesError.push({severity: "danger", summary: "Teléfono incorrecto", detail:"Por favor ingrese su número de teléfono"});
      return;
    }
    if(this.mensaje.mensaje == "" || this.mensaje.mensaje == undefined ){
      this.mensajesError.push({severity: "danger", summary: "Mensaje incorrecto", detail:"Por favor ingrese el mensaje que desea enviarnos"});
      return;
    }

    this.preloader = true;

    this.servicioContacto.NuevoMensaje(this.mensaje).subscribe( 
      cod_mensaje => {
        this.codigo_contacto = cod_mensaje as string;
        this.confirmarMensaje = true;
        this.preloader = false;
      },
      error => {
        let error_mensaje:MensajeError = {severity: "danger", summary: "Error al guardar mensaje", detail:"No se pudo guardar su mensaje, vuelva a intentarlo luego."};
        this.mensajesError = [];
        this.mensajesError.push(error_mensaje);
        this.preloader = false;
      }
    )
  }

  cerrarConfirmacion(){
    this.mensaje = {nombre:"",empresa:"",mail:"",telefono:"",mensaje:""};
    this.confirmarMensaje = false;
  }
}



