import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-aviso-importante',
  templateUrl: './aviso-importante.component.html',
  styleUrls: ['./aviso-importante.component.css']
})
export class AvisoImportanteComponent implements OnInit {

  @Input() titulo:string;
  @Input() detalles:string;
  @Input() styleTitulo:string;
  @Input() styleDetalles:string;

  constructor() { }

  ngOnInit() {
  }
  estiloTitulo(){
    return this.styleTitulo;
  } 

  estiloDetalles(){
    return this.styleDetalles;
  }

}



