import { Component, Output, EventEmitter, Input, OnInit } from '@angular/core';
import { Cliente, Tipo } from '../../../../../interfaces/reserva';
import { MensajeError } from '../../../../../interfaces/mensaje';
import { ValidacionesService } from '../../../../../servicios/validaciones.service';
import { RequestService } from '../../../../../servicios/request.service';

@Component({
  selector: 'app-datos-cliente',
  templateUrl: './datos-cliente.component.html',
  styleUrls: ['./datos-cliente.component.css']
})
export class DatosClienteComponent implements OnInit {

  @Input() cliente:Cliente;
  mensaje_error:MensajeError[] = new Array();

  preloader = false;

  tipos_documento:Tipo[] = [
    {codigo: "C", descripcion: "CÉDULA" },
    {codigo: "R", descripcion: "RUC" },
    {codigo: "E", descripcion: "EXT" }
  ]

  cantones:Tipo[] = [
    { codigo: "1", descripcion: "Quevedo" },
    { codigo: "2", descripcion: "Babahoyo" },
    { codigo: "3", descripcion: "Buena Fe" },
  ]
  cantones_select:any[] = [{label:"Seleccione un cantón",value:null}];

  @Output() clienteSeleccionado:EventEmitter<Cliente>;
  @Output() pasoAtras:EventEmitter<Cliente>;
  @Output() errores:EventEmitter<Array<MensajeError>>;

  constructor(private validaciones:ValidacionesService, private http:RequestService) {
    this.clienteSeleccionado = new EventEmitter();
    this.pasoAtras = new EventEmitter();
    this.errores =  new EventEmitter();
    this.preloader = true;
    
    this.http.get("plantas.listado_de_cantones.php").subscribe(
      cantones => {
        this.cantones = cantones as Tipo[];
        this.cantones.forEach(element => {
          this.cantones_select.push(
            {
              label:element.descripcion,
              value:element
            }
          );
        });
        this.preloader = false;
      },
      error => {
        console.log(error)
        alert("Error al obtener cantones");
        this.preloader = false;
      }
    )
  }

  ngOnInit(){
    
  }

  corregirCaracteresEspeciales(dato:string){
    let caracteres_permitidos = "ABCDEFGHIJKLMNÑOPQRSTUVWXYZabcdefghijklmnñopqrstuvwxyzÁÉÍÓÚáéíóú"
    return dato.trim().toUpperCase();
  }

  seleccionarTipoDocumento(){
    this.tipos_documento.forEach(tipo => {
      if(tipo.codigo == this.cliente.tipo_documento.codigo){
        this.cliente.tipo_documento.descripcion = tipo.descripcion;
      }
    },this);
  }

  seleccionarCliente(){
    // while(this.cliente.numero_documento.length < 13){
    //   this.cliente.numero_documento = "0"+this.cliente.numero_documento;
    // }
    if(this.cliente.tipo_documento.codigo == null){ this.mensaje_error.push({severity:'error', summary:'Tipo de documento', detail:'Debe seleccionar un tipo de documento'}); }
    
    if(this.cliente.tipo_documento.codigo == 'C'){
      //Valido la cédula
      if(this.cliente.numero_documento == null){
        this.mensaje_error.push({severity:'error', summary:'Error en Cédula', detail:'Debe ingresar su número de cédula'});        
      }else if(!this.validaciones.validarCedulaEcuatoriana(this.cliente.numero_documento.toString())){
        this.mensaje_error.push({severity:'error', summary:'Error en Cédula', detail:'La cédula ingresada debe ser válida'});
      }
    }else if(this.cliente.tipo_documento.codigo == 'R'){
      //Valido el ruc
      if(this.cliente.numero_documento == null){
        this.mensaje_error.push({severity:'error', summary:'Error en RUC', detail:'Debe ingresar un número de RUC'});        
      }else if(!this.validaciones.validarRUCEcuatoriana(this.cliente.numero_documento.toString())){
        this.mensaje_error.push({severity:'error', summary:'Error en RUC', detail:'El RUC ingresado debe ser válido'});
      }
    }else if(this.cliente.tipo_documento.codigo == 'E'){
      //Verifico que se haya ingresado un documento extranjero
      if(this.cliente.numero_documento == null || this.cliente.numero_documento == ''){
        this.mensaje_error.push({severity:'error', summary:'Error identificacion', detail:'Debe ingresar un número de identificación'});        
      }else if(this.cliente.numero_documento.length > 20){
        this.mensaje_error.push({severity:'error', summary:'Error identificacion', detail:'El número de identificación no puede superar los 20 dígitos'});        
      }
    }

    if(this.cliente.nombre == null){ this.mensaje_error.push({severity:'error', summary:'Nombre', detail:'Debe ingresar su nombre<br>'}); }
    if(this.cliente.apellido == null || this.cliente.apellido == ''){ this.cliente.apellido == '.' }
    if(this.cliente.telefono == null){ this.mensaje_error.push({severity:'error', summary:'Teléfono', detail:'Debe ingresar su teléfono'}); }
    if(!this.validaciones.validarMail(this.cliente.email)){ this.mensaje_error.push({severity:'error', summary:'Email', detail:'Debe ingresar un email válido'}); }
    if(this.cliente.domicilio == null){ this.mensaje_error.push({severity:'error', summary:'Dirección', detail:'Debe ingresar su domicilio'}); }
    if(this.cliente.ciudad == null){ this.mensaje_error.push({severity:'error', summary:'Ciudad', detail:'Debe ingresar su ciudad<br>Hola'}); }
    if(this.cliente.canton.codigo == null){ this.mensaje_error.push({severity:'error', summary:'Cantón', detail:'Debe ingresar su cantón'}); }

    if(this.mensaje_error.length > 0){
      this.errores.emit(this.mensaje_error);
      this.mensaje_error = []; 
    }else{
      this.clienteSeleccionado.emit(this.cliente);
    }
  }

}



