import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from "./componentes/home/home.component";
import { QuienesSomosComponent } from "./componentes/links/quienes-somos/quienes-somos.component"
import { PorqueNosotrosComponent } from "./componentes/links/porque-nosotros/porque-nosotros.component";
import { ApplusEnElMundoComponent } from "./componentes/links/applus-en-el-mundo/applus-en-el-mundo.component";
import { ContactoComponent } from "./componentes/links/contacto/contacto.component";
import { PlantasComponent } from "./componentes/links/plantas/plantas.component";
import { CalendarioComponent } from "./componentes/links/calendario/calendario.component";
import { TarifasComponent } from "./componentes/links/tarifas/tarifas.component";
import { DocumentacionComponent } from "./componentes/links/documentacion/documentacion.component";
import { RecomendacionesComponent } from "./componentes/links/recomendaciones/recomendaciones.component";
import { NormasComponent } from "./componentes/links/normas/normas.component";
import { ConcursoComponent } from './componentes/links/concurso/concurso.component';
import { BeneficiosComponent } from './componentes/links/beneficios/beneficios.component';
import { DetallesBeneficiosComponent } from './componentes/links/beneficios/detalles-beneficios/detalles-beneficios.component';
import { ReservasComponent } from './componentes/links/reservas/reservas.component';
import { GestionarReservaComponent } from "./componentes/gestionar-reserva/gestionar-reserva.component";
import { EncuestaComponent } from "./componentes/links/encuesta/encuesta.component";

const APP_ROUTES: Routes = [
    { path: '', component: HomeComponent },
    { path: 'applus', component: QuienesSomosComponent },
    { path: 'applus_en_el_mundo', component: ApplusEnElMundoComponent },
    { path: 'contacto', component: ContactoComponent },
    { path: 'plantas', component: PlantasComponent },
    { path: 'plantas/:id', component: PlantasComponent },
    { path: 'calendario', component: CalendarioComponent },
    { path: 'tarifas', component: TarifasComponent },
    { path: 'documentacion', component: DocumentacionComponent },
    { path: 'recomendaciones', component: RecomendacionesComponent },
    { path: 'concurso', component: ConcursoComponent },
    { path: 'beneficios', component: BeneficiosComponent },
    { path: 'beneficios/:id', component: DetallesBeneficiosComponent },
    { path: 'normas', component: NormasComponent },
    { path: 'reservas', component: ReservasComponent },
    { path: 'encuesta', component: EncuestaComponent },
    { path: 'gestionar_reserva', component: GestionarReservaComponent },
    //{ path: 'staticPath', component: ... },
    //{ path: '**', component: ... },
    //{ path: 'oldPath', redirectTo: '/staticPath' },
    //{ path: ..., component: ..., data: { message: 'Custom' }
];false

export const APP_ROUTING = RouterModule.forRoot(APP_ROUTES, {useHash:true});





