import { Component, OnInit , ViewChild, ElementRef, Input, AfterViewInit} from '@angular/core';
import { PlantasService } from '../../../servicios/plantas.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Planta } from '../../../interfaces/planta';

///<reference path="../node_modules/angular2/typings/browser.d.ts"/>

declare const Microsoft: any;

@Component({
  selector: 'app-plantas',
  templateUrl: './plantas.component.html',
  styleUrls: ['./plantas.component.css']
})
export class PlantasComponent implements OnInit { 

  public ventana;
  public animacion_auto = false;
  public altura_contenido:string;
  public plantas:Planta[] = new Array<Planta>();
  public porcentajeAlturaMain:number =  0.4;
  public map_center = {
    lat:0,
    lng:0
  }
  public horarioPlanta;
  public nombrePlanta;
  public user_location:any;
  public id_planta_seleccionada:string;
  public markers = [];
  public zoom:number = 12;

  public map:any;

    GetMap(latitud:number,longitud:number)
    {
        this.map = new Microsoft.Maps.Map('#myMap', {
          // credentials: this._apikey,
          center: new Microsoft.Maps.Location(latitud,longitud),
          mapTypeId: Microsoft.Maps.MapTypeId.canvasLight
        });

        var locCol = new Microsoft.Maps.Location(20.610332,-103.398110);       
        var locVall = new Microsoft.Maps.Location(20.690396,-105.233193);
        var locGuz = new Microsoft.Maps.Location(19.697483,-103.485771);      
        var locEls = new Microsoft.Maps.Location(20.500933,-103.231784);  
           
        // Add a pin to the map
        var pin = new Microsoft.Maps.Pushpin(locCol, {title: 'Planta Colón'}); 
        this.map.entities.push(pin);       
        var pin = new Microsoft.Maps.Pushpin(locVall, {title: 'Planta Vallarta'}); 
        this.map.entities.push(pin);
        var pin = new Microsoft.Maps.Pushpin(locGuz, {title: 'Planta Guzmán'}); 
        this.map.entities.push(pin);
        var pin = new Microsoft.Maps.Pushpin(locEls, {title: 'Planta El Salto'}); 
        this.map.entities.push(pin);

        // Center the map on the location
        this.map.setView({center: this.map.center, zoom: 13});
    }

  constructor(public servicioPlantas:PlantasService, private router:Router, private route:ActivatedRoute) { 
  }

  //Durán en DB -2.192878,-79.812292
  //Portoviejo -1.076963, -80.505274
  ngOnInit() {
    this.ventana = this.Ventana();
    this.altura_contenido = this.getStyle();

    this.route.params.subscribe(datos=>{
      this.id_planta_seleccionada = datos.id
      this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    });
    // console.log(this.id_planta_seleccionada);
    this.servicioPlantas.ObtenerListadoPlantas().subscribe(
      (datos:Array<Planta>) => {
        datos.forEach((planta)=>{this.plantas.push(planta);})

        this.plantas.forEach((planta:Planta)=>{
          let object:Planta = {
            id: planta.id,
            codigo: planta.codigo,
            nombre: planta.nombre,
            cod_empresa:planta.cod_empresa,
            tipo:planta.tipo,
            lat: Number(planta.lat),
            lng: Number(planta.lng),
            direccion:planta.direccion,
            ciudad:planta.ciudad,
            nro_contacto:planta.nro_contacto,
            imagen: planta.imagen
          }
          this.markers.push(object);
          // console.log("MARKERS", this.markers)
          this.GetMap(this.servicioPlantas.getPlanta(this.id_planta_seleccionada).lat,this.servicioPlantas.getPlanta(this.id_planta_seleccionada).lng);
          //  this.centrarMapa();
        })
      }, 
      error => {console.error(error)}
    );

    // console.log('Planta');
    // console.log(this.id_planta_seleccionada);
    if(this.id_planta_seleccionada == 'colon'){
      this.nombrePlanta = 'CVR 0010 Colón';
    }
    else if(this.id_planta_seleccionada == 'vallarta'){
      this.nombrePlanta = 'CVR 0007 Vallarta';
    }
    else if(this.id_planta_seleccionada == 'guzman'){
      this.nombrePlanta = 'CVR 0012 Guzmán';
    }
    else if(this.id_planta_seleccionada == 'el_salto'){
      this.nombrePlanta = 'CVR 000 El Salto';
    }

    // console.log(this.horarioPlanta);
    if(this.id_planta_seleccionada == 'colon'){
      this.horarioPlanta = 'De Lunes a Viernes de 8:00 a 20:00hs <br>Sábados de 8:00 a 16:00hs';
    }
    else if(this.id_planta_seleccionada == 'vallarta'){
      this.horarioPlanta = 'De Lunes a Viernes de 9:00 a 18:00hs <br>Sábados de 9:00 a 14:00hs';
    }
    else if(this.id_planta_seleccionada == 'guzman'){
      this.horarioPlanta = 'De Lunes a Viernes de 9:00 a 18:00hs <br>Sábados de 9:00 a 14:00hs';
    }
    else if(this.id_planta_seleccionada == 'el_salto'){
      this.horarioPlanta = '<b>PROXIMAMENTE:</b> <br><b>APERTURA ESTIMADA ENERO 2023</b>';
    }
  }

  /** Ventana
   * Obtiene el tamaño de la ventana y lo guarda en la variable ventana
   */
  Ventana(){
    let ancho;
    let alto;
    if (!window.innerHeight) { ancho = (document.body.clientWidth); alto = (document.body.clientHeight); }
    else{ ancho = (window.innerWidth); alto = (window.innerHeight); }
    return { ancho:ancho, alto:alto };
  }

  /** getStyle
   * Establece el alto del mapa según el alto de la ventana y el del main
   */
  getStyle(){
    let estilos:string = "";
    // REM => PIXEL
    //1rem * CantRems = CantPixels
    let pixeles_a_dejar_vacios = 2 * parseFloat(getComputedStyle(document.documentElement).fontSize);
    let alto:number = Math.round( ( this.ventana.alto - document.querySelector('header').scrollHeight ) * ( 1 - this.porcentajeAlturaMain ) - pixeles_a_dejar_vacios );
    //estilos += "height: "+ alto +"px;";
    estilos += "height: 300px;";
    return estilos;
  }

  /** getUserPosition
   * Obtiene la posición  actual del usuario y lo guarda en la variable user_location
   */
  getUserPosition(){
    if(navigator.geolocation){
      navigator.geolocation.getCurrentPosition(position => {
        this.user_location = position.coords;
      });
    }
  }

  /** ComoLlegar
   * Abre una nueva ventana con la ruta desde la posición del cliente hacia la planta en google maps
   */
  ComoLlegar(){
    this.getUserPosition();
    let planta = this.servicioPlantas.getPlanta(this.id_planta_seleccionada);
    window.open("https://www.google.com.ar/maps/dir//" + planta.lat + "," + planta.lng + "/data=!3m1!4b1!4m2!4m1!3e0");
  }

  /** centrarMapa
   * Centra el mapa respecto de las plantas en el servicio de plantas
   */
 /*  centrarMapa(){
    this.servicioPlantas.plantas.map((planta,indice,array:any[])=>{
      this.map_center.lat += Math.round(planta.lat*1000)/1000;
      this.map_center.lng += Math.round(planta.lng*1000)/1000;
      let divisor = ( indice > 0 ) ? 2 : 1 ;
      this.map_center.lat = this.map_center.lat / divisor;
      this.map_center.lng = this.map_center.lng / divisor;
    },this);
  } */
/* 
  redirigir(ruta:string){
    let detalles_planta = document.querySelector("#detalles");
    detalles_planta.scrollIntoView({behavior: "smooth",block:"start"})
    this.router.navigate([ruta]);
  } */
}




