import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { HorarioTurnos } from '../../../../../interfaces/horario-turnos';
import { Turno } from '../../../../../interfaces/reserva';
import { PlantasService } from '../../../../../servicios/plantas.service';
import { Planta } from '../../../../../interfaces/planta';
import { MensajeError } from '../../../../../interfaces/mensaje';

@Component({
  selector: 'app-selector-de-turno',
  templateUrl: './selector-de-turno.component.html',
  styleUrls: ['./selector-de-turno.component.css']
})
export class SelectorDeTurnoComponent implements OnInit {
  @Input() turnoSeleccionado:Turno;
  
  @Output() devolverTurno:EventEmitter<Turno>;
  @Output() pasoAtras:EventEmitter<Turno>;
  @Output() errores:EventEmitter<Array<MensajeError>>;

  mensaje_error:MensajeError[] = [];

  //Variables para el manejo  de fechas en el calendario
  public fecha:Date = new Date(Date.now() + 86400);
  public fecha_min:Date = new Date( (new Date(Date.now())).toDateString() );
  public fecha_max:Date= new Date( (new Date(Date.now())).toDateString() );
  public fechas_inaccesibles:Date[] =[];
  public calendario_es:any = {
    firstDayOfWeek: 0,
    dayNames: ["Domingo", "Lunes", "Martes", "Miércoles", "Jueves", "Viernes", "Sábado"],
    dayNamesShort: ["Dom", "Lun", "Mar", "Mie", "Jue", "Vie", "Sab"],
    dayNamesMin: ["Do","Lu","Ma","Mi","Ju","Vi","Sa"],
    monthNames: [ "Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto","Septiembre","Octubre","Noviembre","Diciembre" ],
    monthNamesShort: [ "Ene", "Feb", "Mar", "Abr", "May", "Jun","Jul", "Ago", "Sep", "Oct", "Nov", "Dic" ],
    today: 'Hoy',
    clear: 'Limpiar'
  };

  //Variables para la selección del turno
  public franja_horaria:string;
  public horas_disponibles:HorarioTurnos[] = []
  

  constructor(public plantasService:PlantasService) {
    let pc_local = localStorage.getItem("ESLOCAL");
    if(pc_local == undefined){
      this.fecha_min.setSeconds(86400);
      console.log("pasa")
    }
    this.fecha_max.setSeconds(59*86400); //Cantidad de días * milisegundos por día

    this.devolverTurno = new EventEmitter();
    this.pasoAtras = new EventEmitter();
    this.errores = new EventEmitter();
  }

  ngOnInit(){
    if(this.turnoSeleccionado.fecha != ""){
      this.fecha = new Date(parseInt(this.turnoSeleccionado.fecha.slice(6,10)),parseInt(this.turnoSeleccionado.fecha.slice(3,5))-1,parseInt(this.turnoSeleccionado.fecha.slice(0,2)))
      if(this.turnoSeleccionado.hora != ""){
        this.franja_horaria = (parseInt(this.turnoSeleccionado.hora.split(":")[0]) >= 12) ? "tarde" : "mañana";
      }
    }

    this.plantasService.ObtenerTurnosPlanta(this.turnoSeleccionado.planta.id).subscribe(
      turnos => {
        this.horas_disponibles = turnos as HorarioTurnos[];
        this.fechasInaccesibles();
      }
    )
  }

  fechasInaccesibles(){
    let fecha_actual = new Date( (new Date(Date.now())).toDateString() );
    while(fecha_actual.getTime() <= this.fecha_max.getTime()){
      // console.log("---------------------------");
      // console.log("fecha actual: ",fecha_actual);

      let existe = false;
      for(let i = 0; i< this.horas_disponibles.length ; i++){
        //console.log(parseInt(this.horas_disponibles[i].fecha.slice(0,4)),parseInt(this.horas_disponibles[i].fecha.slice(5,7)),parseInt(this.horas_disponibles[i].fecha.slice(8,10)));
        let fecha_turno = new Date(parseInt(this.horas_disponibles[i].fecha.slice(0,4)),parseInt(this.horas_disponibles[i].fecha.slice(5,7))-1,parseInt(this.horas_disponibles[i].fecha.slice(8,10)));
        // console.log("fecha turno: ",fecha_turno);
        
        if(fecha_actual.toDateString() == fecha_turno.toDateString() ){
          existe = true;
          break;
        }
      }
      if( !existe ){
        let fecha_agregar = new Date(fecha_actual.getTime());
        // console.log("ENTRA: ",fecha_actual,fecha_agregar)
        this.fechas_inaccesibles.push( fecha_agregar );
      }
      fecha_actual.setSeconds(1*86400);
    }
  }

  formatoFecha(){
    let dia = this.fecha.getDate().toString().length < 2 ? "0" + this.fecha.getDate().toString() : this.fecha.getDate().toString();
    let mes = (this.fecha.getMonth()+1).toString().length < 2 ? "0" + (this.fecha.getMonth()+1).toString() : (this.fecha.getMonth()+1).toString();
    let anio = this.fecha.getFullYear();
    return dia + "/" + mes + "/" + anio;
  }

  cambioDeFecha(){
    this.franja_horaria = null;
    this.turnoSeleccionado.hora = null;
  }

  mostrarHora(hora){
    if((this.franja_horaria == hora.turno) && (this.fecha.toJSON().slice(0,10) == hora.fecha.slice(0,10))){
      return true;
    }else{
      return false;
    }
  }
  
  setClaseBoton(hora:HorarioTurnos){
    if(hora.hora == this.turnoSeleccionado.hora){ return "d-inline-block btn-horas-active px-2"; }
    else{ return "d-inline-block btn-horas px-2 "; }
  }

  seleccionarHoraYFecha(hora:any){
    this.turnoSeleccionado.hora = hora.hora;
    this.turnoSeleccionado.fecha = this.formatoFecha();
  }

  tomarTurno(){
    if( this.turnoSeleccionado.fecha == "" ){
      this.mensaje_error.push({severity:'error', summary:'Fecha erronea', detail:'Debe seleccionar una fecha disponible'});
    }

    if( this.turnoSeleccionado.hora == "" ){
      this.mensaje_error.push({severity:'error', summary:'Hora erronea', detail:'Debe seleccionar una hora disponible'});
    }
    if(this.mensaje_error.length > 0){
      this.errores.emit(this.mensaje_error);
      this.mensaje_error = []; 
    }else{
      this.devolverTurno.emit(this.turnoSeleccionado)
    }
  }
}



