import { Component, OnInit } from '@angular/core';
import { Beneficio } from '../../../interfaces/beneficios';
import { BeneficiosService } from "../../../servicios/beneficios.service";

@Component({
  selector: 'app-beneficios',
  templateUrl: './beneficios.component.html',
  styleUrls: ['./beneficios.component.css']
})
export class BeneficiosComponent implements OnInit {

  public localidades:string[] = ['Durán','Guayaquil','Samborondón']

  public beneficios:Beneficio[];

  constructor(public beneficiosService:BeneficiosService) { 
    this.beneficiosService.PedirBeneficios().subscribe(
      beneficios_descargados => {
        this.beneficios = beneficios_descargados as Beneficio[];
      }
    )
  }

  ngOnInit() {
  }

  mostrarBeneficio(localidad:string, localidad_beneficio:string){
    if(localidad == localidad_beneficio){
      let estilo_localidad = "padding: 1rem;"
      //estilo_localidad = estilo_localidad + "padding-right: 0;"
      //estilo_localidad = estilo_localidad + "margin-rigth:1rem;"
      //estilo_localidad = estilo_localidad + "border-bottom: 1px solid #FC6500;"
      return estilo_localidad;
    }else{
      return "display: none;"
    }
  }
}





