import { Injectable } from '@angular/core';
import { Planta,CronogramaPlanta } from "../interfaces/planta";
import { RequestService } from './request.service';
import 'rxjs/add/operator/map';


@Injectable()
export class PlantasService {

  public plantas:Planta[] = [];

  public cronograma_moviles:CronogramaPlanta[] = []

  public turnos_disponibles:any[] = [];

  constructor(private req:RequestService) {
  
    this.ObtenerListadoPlantas().subscribe();
    this.ObtenerCronogramasMoviles().subscribe();
  }

  getPlanta(id:string){
    for (const planta of this.plantas) {
      if(planta.codigo == id){
        return planta;
      }
    }
  }

  public ObtenerListadoPlantas(){
    return this.req.get("plantas.listado_de_plantas.php").map(
      resultado => {
        // console.log(resultado)
        this.plantas = resultado as Planta[];
        return resultado;
      }
    ,this);
  }

  public ObtenerListadoPlantasConTurno(){
    return this.req.get("plantas.plantas_con_turno.php").map(
      resultado => {
        // console.log(resultado)
        this.plantas = resultado as Planta[];
        return resultado;
      }
    ,this);
  }


  public ObtenerCronogramasMoviles(){
    return this.req.get("plantas.cronograma_moviles.php").map(
      resultado => { 
        this.cronograma_moviles = resultado as CronogramaPlanta[];
        // console.log(this.cronograma_moviles);
        return resultado;
      }
    ,this);
  }

  public ObtenerTurnosPlanta(id_planta){
    return this.req.get("plantas.turnos_disponibles.php",{ id_planta: id_planta }).map(
      resultado => { 
        this.turnos_disponibles = resultado as Array<any>;
        return resultado;
      }
    ,this);
  }
}


