import { Injectable } from '@angular/core';

@Injectable()
export class ValidacionesService {

  constructor() { }

  public validaPatente(patente) {
    if (patente.length == 7) {
        //Valida autos patente XXX 1111
        if (this.alfabetico(patente[0]) && this.alfabetico(patente[1]) && this.alfabetico(patente[2]) && this.numerico(patente[3]) && this.numerico(patente[4]) && this.numerico(patente[5]) && this.numerico(patente[6]))
            return true;
    }

    if (patente.length == 6){
      //Valida autos patente XX 111 X
      if (this.alfabetico(patente[0]) && this.alfabetico(patente[1]) && this.numerico(patente[2]) && this.numerico(patente[3]) && this.numerico(patente[4]) && this.alfabetico(patente[5]) )
      return true;
    }
    return false;
  }

  public calcularDVCIUruguay(ci){
    var a = 0;
    var i = 0;
    if(ci.length <= 6){
      for(i = ci.length; i < 7; i++){
        ci = '0' + ci;
      }
    }
    for(i = 0; i < 7; i++){
      a += (parseInt("2987634"[i]) * parseInt(ci[i])) % 10;
    }
    if(a%10 === 0){
      return 0;
    }else{
      return 10 - a % 10;
    }
  }
  
  public validarCIUruguay(ci){
    ci = ci.replace(/\D/g, '');
    var dig = ci[ci.length - 1];
    ci = ci.replace(/[0-9]$/, '');
    return (dig == this.calcularDVCIUruguay(ci));
  }

  public validarMail(mail) {
      var emailRegex = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
      if (!emailRegex.test(mail)) {
          return false;
      }
      return true;
  }

  public alfabetico(car) {
    var alfa = "ABCDEFGHIJKLMNÑOPQRSTUVWXYZabcdefghijklmnñopqrstuvxyz";
    return (alfa.indexOf(car) != -1);
  }

  public numerico(car) {
    var num = "0123456789";
    return (num.indexOf(car) != -1);
  }

  public validarCedulaEcuatoriana(cedula:string){
    //La cédula debe tener 10 dígitos
    if(cedula.length == 9){ 
      let cero:string = '0';
      cedula = cero.concat(cedula);
    }
    if(cedula.length != 10){ return false; }

    let c_array:string[] = cedula.split('');

    //Los primeros dos digitos representan la provincia (1 - 24) y 30 para ecuatorianos viviendo en el extranjero 
    if( ( parseInt(c_array[0] + c_array[1]) < 0 || parseInt(c_array[0] + c_array[1]) > 24 ) && ( (c_array[0] + c_array[1]) != '30') ){ return false; }
    //El tercer dígito es un número menor a 6
    if( parseInt(c_array[2]) > 6){ return false; }

    //Coeficientes de verificación
    let coeficientes:number[] = [2,1,2,1,2,1,2,1,2];
    //Acumulados de coeficiente * número en cédula
    let resultado:number = 0;

    for(let i = 0; i < 9; i++){ 
      let parcial = coeficientes[i] * parseInt(c_array[i]);
      if(parcial >= 10){ parcial = parcial - 9; }
      resultado += parcial;
    }

    let decena_superior:number = (Math.trunc(resultado /10) + 1) * 10;
    let dv_calculado:number = decena_superior - resultado;
    dv_calculado = (dv_calculado == 10)? 0:dv_calculado;
    if( dv_calculado == parseInt(c_array[9]) ){ return true; }

    return false;
  }
  
  public validarRUCEcuatoriana2(ruc:string){
    //El RUC debe tener 13 dígitos
    if(ruc.length != 13){ return false; }

    let ruc_array:string[] = ruc.split('');

    if(parseInt(ruc_array[2]) < 6){
      //PERSONA NATURAL
      return this.validarCedulaEcuatoriana(ruc.slice(0,10));

    }else if(ruc_array[2] == '6'){
      //Personas Públicas o Entidades Estatales
      let dv:string = ruc_array[8];
      let coeficientes:number[] = [3,2,7,6,5,4,3,2];
      let suma:number = 0;
      for(let i=0; i<8;i++){ suma += coeficientes[i] * parseInt(ruc_array[i]); }
      let dv_calculado = 11 - (suma%11);
      
      if(dv_calculado == parseInt(dv)){ return true; }
      return false;
    }else if(ruc_array[2] == '9'){
      //Sociedades privadas y extranjeros no residentes (sin cédula de identidad)
      let dv:string = ruc_array[8];
      let coeficientes:number[] = [4,3,2,7,6,5,4,3,2];
      let suma:number = 0;
      for(let i=0; i<8;i++){ suma += coeficientes[i] * parseInt(ruc_array[i]); }
      let dv_calculado = 11 - (suma%11);
      
      if(dv_calculado == parseInt(dv)){ return true; }
      return false;

    }
  }

  public validarRUCEcuatoriana(ruc:string){
    let number = ruc
    let dto = number.length
    let valor
    let acu=0
    if(number==""){
     return false;
     }
    else{
     for (let i=0; i<dto; i++){
      valor = number.substring(i,i+1);
      if(valor==0||valor==1||valor==2||valor==3||valor==4||valor==5||valor==6||valor==7||valor==8||valor==9){
        acu = acu+1;
      }
     }
     if(acu==dto){
      while(number.substring(10,13)!='001'){
       return false;
      }
      while(number.substring(0,2) >'24'){    
       return false;
      }
      //alert('El RUC está escrito correctamente');
      //alert('Se procederá a analizar el respectivo RUC.');
      let porcion1 = number.substring(2,3);
      if(porcion1<'6'){
       //alert('El tercer dígito es menor a 6, por lo \ntanto el usuario es una persona natural.\n');
      }
      else{
       if(porcion1=='6'){
        //alert('El tercer dígito es igual a 6, por lo \ntanto el usuario es una entidad pública.\n');
       }
       else{
        if(porcion1=='9'){
         //alert('El tercer dígito es igual a 9, por lo \ntanto el usuario es una sociedad privada.\n');

        }
       }
      }
     }
     else{
     //alert("ERROR: Por favor no ingrese texto");
     return false;
     }
    }
    return true;
   }

}


