import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Reserva,Tarifa } from '../../../../../interfaces/reserva';
import { MensajeError } from "../../../../../interfaces/mensaje";
import { RequestService } from '../../../../../servicios/request.service';
import { stringify } from '@angular/core/src/util';
import { CaptchaService } from '../../../../../servicios/captcha.service';
import { environment } from '../../../../../../environments/environment';

@Component({
  selector: 'app-confirmar-turno',
  templateUrl: './confirmar-turno.component.html',
  styleUrls: ['./confirmar-turno.component.css']
})
export class ConfirmarTurnoComponent implements OnInit {
  public preloader=false;
  public googleKey: string = null;
  public error_a_enviar = new Array<MensajeError>();/*
  public montoRTV = 0;
  public montoOblea = 0;
  public montoTotal = 0;*/
  @Input() datosReserva:Reserva;
  @Output() confirmado:EventEmitter<boolean> = new EventEmitter();
  @Output() pasoAtras:EventEmitter<boolean> = new EventEmitter();
  @Output() errores:EventEmitter<Array<MensajeError>> = new EventEmitter();
  constructor(private req:RequestService,private captchaService: CaptchaService) { 
    this.googleKey = environment.googleSiteKey;
  }
  ngOnInit(){
    this.obtenerTarifa();
  }
  obtenerTarifa(){
    this.datosReserva.tarifa = new Tarifa();
    let params = {
      tipo_vehiculo:this.datosReserva.vehiculo.tipo_vehiculo.codigo,
      patente: this.datosReserva.vehiculo.placa
    }
    this.preloader = true;
    
    this.req.get("reservas.get_tarifa.php",params).subscribe(
      exito => {
        this.datosReserva.tarifa = exito as Tarifa;
        this.preloader = false;
      },
      error=>{
        this.preloader = false;
        this.error_a_enviar = [];
        this.error_a_enviar.push({severity:"error",summary:"Error al generar tarifa",detail:"Tuvimos un error al generar la tarifa, por favor vuelva a intentarlo luego."})
        this.errores.emit(this.error_a_enviar);
        this.pasoAtras.emit();
      }
    );
  }
  
  public captchaResolved(resolved: string){
    this.captchaService.setCaptchaToken(resolved);
  }


  confirmar(){
    this.error_a_enviar = [];
    if(this.datosReserva.como_nos_conocio == null){
      this.error_a_enviar.push({severity:"info",summary:"",detail:"Por favor, indique la forma en la que conoció nuestro Centro de Verificación."})
      this.errores.emit(this.error_a_enviar);
      return;
    }
    this.confirmado.emit(true)
  }
}




