import { Planta } from "./planta";


export class Reserva{
    vehiculo:Vehiculo
    cliente:Cliente
    turno:Turno
    codigo_reserva:string
    tarifa:Tarifa
    como_nos_conocio:string
    constructor(){
        this.cliente = new Cliente();
        this.vehiculo = new Vehiculo();
        this.turno = new Turno();
        this.tarifa = new Tarifa();
        this.como_nos_conocio = null;
    }
}

export class Cliente{
    tipo_documento:Tipo
    numero_documento:any
    nombre:string
    apellido:string
    domicilio:string
    ciudad:string
    canton:Tipo
    telefono:string
    email:string
    constructor(){
        this.tipo_documento = new Tipo();
        this.canton = new Tipo();
    }
}

export class Vehiculo{
    placa:string
    anio:string
    vin:string
    marca:Tipo
    modelo:Tipo
    tipo_vehiculo:Tipo
    tipo_especie:Tipo
    tipo_destino:Tipo

    constructor(){
        this.tipo_vehiculo = new Tipo();
        this.tipo_especie = new Tipo();
        this.tipo_destino = new Tipo();
        this.marca = new Tipo();
        this.modelo = new Tipo();
    }
}

export class Turno{
    planta:Planta
    fecha:string
    hora:string
    constructor(){
        this.fecha = "";
        this.hora = "";

    }
}

export class Tarifa{
    oblea:number
    montoTotal:number
    reasigna_turno:boolean
    nro_verificacion:number
    monto:number
    iva:number    
}

export class Tipo{
    codigo:string
    descripcion:string
}


