import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Vehiculo, Tipo } from '../../../../../interfaces/reserva';
import { MensajeError } from '../../../../../interfaces/mensaje';
import { ValidacionesService } from '../../../../../servicios/validaciones.service';
import { RequestService } from '../../../../../servicios/request.service';



@Component({
  selector: 'app-datos-vehiculo',
  templateUrl: './datos-vehiculo.component.html',
  styleUrls: ['./datos-vehiculo.component.css']
})
export class DatosVehiculoComponent implements OnInit {

  @Input() vehiculo:Vehiculo;
  public mensaje_error:MensajeError[] = new Array();
  public preloader:boolean = false;
  public anios:Array<number>;
  public tipos_vehiculos:Tipo[] = [ {codigo:'-1', descripcion:"Seleccione"} ]
  public tipos_especie:Tipo[] = [ {codigo:'-1', descripcion:"Seleccione"} ]
  public tipos_destino:Tipo[]  = [ {codigo:'-1', descripcion:"Seleccione"} ]
  public marcas:Tipo[]  = [ {codigo:'-1', descripcion:"Seleccione"} ]
  public modelos:Tipo[]  = [ {codigo:'-1', descripcion:"Seleccione"} ]

  @Output() vehiculoSeleccionado:EventEmitter<Vehiculo>;
  @Output() pasoAtras:EventEmitter<Vehiculo>;
  @Output() errores:EventEmitter<Array<MensajeError>>;

  constructor(public validaciones:ValidacionesService, private http:RequestService) { 
    this.vehiculoSeleccionado = new EventEmitter();
    this.pasoAtras = new EventEmitter();
    this.errores = new EventEmitter();
    this.anios = new Array<number>();
  }

  ngOnInit(){
    this.preloader = true;
    this.tipos_vehiculos = [ {codigo:'1', descripcion:"Seleccione"} ];
    this.tipos_especie = [ {codigo:'-1', descripcion:"Seleccione"} ];
    this.tipos_destino = [ {codigo:'1', descripcion:"Seleccione"} ];
    this.marcas = [ {codigo:'1', descripcion:""} ];
    this.modelos = [ {codigo:'1', descripcion:""} ];

    this.vehiculo.tipo_vehiculo.codigo = '1';
    this.vehiculo.tipo_especie.codigo = '-1';
    this.vehiculo.tipo_destino.codigo = '1';
    this.vehiculo.modelo.codigo = '1';
    this.vehiculo.marca.codigo = '1';

    this.preloader = true;

    //EL TIPO DE VEHICULO ES EL TIPOESPECIE!
    this.http.get("reservas.get_tipo_vehiculo.php").subscribe(
      exito => {
        (exito as Tipo[]).forEach(element => {
          this.tipos_vehiculos.push(element);
        },this);
        this.preloader = false;
      },
      error => { 
        console.log(error);
        this.mensaje_error = [];
        this.mensaje_error.push({severity:"error", summary:"Error", detail:"Ocurrió un error. Vuelva a intentar luego."});
        this.preloader = false;
      }
    )

    /**Cargo el array de años para el seleccionador */
    let año:any = new Date();
    let año_actual = año.getFullYear();
    año = año.getFullYear() + 1;
    while(año != (año_actual - 200)){
      this.anios.push(año);
      año = año -1;
      if(año == 1900){
        break;
      }
    }
  }

  seleccionarMarcaVehiculo(){
    this.vehiculo.modelo.codigo = '-1';
    this.modelos = [ {codigo:'-1', descripcion:""} ];
    
    this.preloader = true;
    this.marcas.forEach(tipo => {
      if(tipo.codigo == this.vehiculo.marca.codigo){
        this.vehiculo.marca.descripcion = tipo.descripcion;
        let parametros = {
          marca : tipo.codigo
        }
        this.http.get("reservas.get_modelos.php",parametros).subscribe(
          exito => {
            (exito as Tipo[]).forEach(element => {
              this.modelos.push(element);
            },this);
            this.preloader = false;
          },
          error => { 
            console.log(error);
            this.mensaje_error = [];
            this.mensaje_error.push({severity:"error", summary:"Error", detail:"Ocurrió un error. Vuelva a intentar luego."});
            this.preloader = false;
          }
        )
      }
    },this);
  }

  seleccionarModeloVehiculo(){
    this.modelos.forEach(tipo => {
      if(tipo.codigo == this.vehiculo.modelo.codigo){
        this.vehiculo.modelo.descripcion = tipo.descripcion;
      }
    },this);
  }

  seleccionarTipoVehiculo(){
    this.vehiculo.tipo_especie.codigo = '1';
    this.vehiculo.tipo_destino.codigo = '1';
    this.tipos_especie = [ {codigo:'1', descripcion:""} ];
    this.tipos_destino = [ {codigo:'1', descripcion:""} ];

    this.tipos_vehiculos.forEach(tipo => {
      if(tipo.codigo == this.vehiculo.tipo_vehiculo.codigo){
        this.vehiculo.tipo_vehiculo.descripcion = tipo.descripcion;
        /*
        let parametros = {
          tipo_vehiculo : tipo.codigo
        }
        this.preloader = true;
        this.http.get("reservas.get_tipo_espe_vehiculo.php",parametros).subscribe(
          exito => {
            (exito as Tipo[]).forEach(element => {
              this.tipos_especie.push(element);
            },this);
            this.preloader = false;
          },
          error => { 
            console.log(error);
            this.mensaje_error = [];
            this.mensaje_error.push({severity:"error", summary:"Error", detail:"Ocurrió un error. Vuelva a intentar luego."});
            this.preloader = false;
          }
        )*/
      }
    },this);
  }

  seleccionarTipoEspecie(){
    this.vehiculo.tipo_destino.codigo = '-1';
    this.tipos_destino = [ {codigo:'-1', descripcion:"Seleccione"} ];
    this.tipos_especie.forEach(tipo => {
      if(tipo.codigo == this.vehiculo.tipo_especie.codigo){
        this.vehiculo.tipo_especie.descripcion = tipo.descripcion;
        let parametros = {
          tipo_especie : tipo.codigo
        }
        /*
        this.preloader = true;
        this.http.get("reservas.get_tipo_destino_vehiculo.php",parametros).subscribe(
          exito => {
            (exito as Tipo[]).forEach(element => {
              this.tipos_destino.push(element);
            },this);
            this.preloader = false;
          },
          error => { 
            console.log(error);
            this.mensaje_error = [];
            this.mensaje_error.push({severity:"error", summary:"Error", detail:"Ocurrió un error. Vuelva a intentar luego."});
            this.preloader = false;
          }
        )
        */
      }
    },this);
  }
  
  seleccionarTipoDestino(){
    this.tipos_destino.forEach(tipo => {
      if(tipo.codigo == this.vehiculo.tipo_destino.codigo){
        this.vehiculo.tipo_destino.descripcion = tipo.descripcion;
      }
    },this);
  }

  vehiculoCargado(){
    if(this.vehiculo.placa == null || !this.validaciones.validaPatente(this.vehiculo.placa)){
      this.mensaje_error.push({severity:'error', summary:'Error en placa', detail:'La placa debe respetar el formato de tres letras y cuatro números.'});
    }

    if(this.vehiculo.vin == null || this.vehiculo.vin == ''){
      this.mensaje_error.push({severity:'error', summary:'Error en VIN', detail:'Debe ingresarse el numero de VIN del vehìculo.'});
    }

    if(this.vehiculo.anio == null || this.vehiculo.anio == ''){
      this.mensaje_error.push({severity:'error', summary:'Error en año', detail:'Debe seleccionar el año de fabricación del vehìculo.'});
    }

    if(this.vehiculo.marca.descripcion == null || this.vehiculo.marca.descripcion == ""){
      this.mensaje_error.push({severity:'error', summary:'Marca de vehículo', detail:'Debe seleccionar una marca de vehículo'});
    }

    if(this.vehiculo.modelo.descripcion == null || this.vehiculo.modelo.descripcion == ""){
      this.mensaje_error.push({severity:'error', summary:'Modelo de vehículo', detail:'Debe seleccionar un modelo de vehículo'});
    }

    // if(this.vehiculo.tipo_vehiculo.codigo == null || parseInt(this.vehiculo.tipo_vehiculo.codigo) < 0){
    //   this.mensaje_error.push({severity:'error', summary:'Tipo de vehículo', detail:'Debe seleccionar un tipo de vehículo'});
    // }

    if(this.vehiculo.tipo_especie.codigo == null || parseInt(this.vehiculo.tipo_especie.codigo) < 0){
      this.mensaje_error.push({severity:'error', summary:'Clase de vehículo', detail:'Debe seleccionar una clase de vehículo'});
    }
    
    // if(this.vehiculo.tipo_destino.codigo == null || parseInt(this.vehiculo.tipo_destino.codigo) < 0){
    //   this.mensaje_error.push({severity:'error', summary:'Tipo de destino', detail:'Debe seleccionar un tipo de destino'});
    // }

    if(this.mensaje_error.length > 0){
      this.errores.emit(this.mensaje_error);
      this.mensaje_error = []; 
    }else{
      //Todo validado, verifico que no tenga turno
      this.preloader = true;
      let params = { patente: this.vehiculo.placa };
      this.http.get("reservas.consultar_patente.php",params).subscribe(
        exito => {
          if(exito == true){
            //Tiene turno
            this.mensaje_error.push({severity:'error', summary:'Turno vigente', detail:'Este vehículo ya tiene un turno vigente.'});
            this.errores.emit(this.mensaje_error);
            this.mensaje_error = []; 
          }else{
            this.vehiculoSeleccionado.emit(this.vehiculo)
          }
          this.preloader = false;
        },
        error => {
          this.mensaje_error.push({severity:'error', summary:'Turno vigente', detail:'No se pudo consultar la vigencia de turnos para la placa seleccionada, por favor vuelva a intentarlo luego.'});
          this.errores.emit(this.mensaje_error);
          this.mensaje_error = []; 
          this.preloader = false;
        }
      )
    }
  }

  GetTipoVehiculo(){
    
  }

}




