import { Injectable } from '@angular/core';

@Injectable()
export class CaptchaService {

  private captchaToken: string = null;

  constructor() { }

  public setCaptchaToken(token: string) {
    this.captchaToken = token;
  }

  public getCaptchaToken(){
    return this.captchaToken;
  }

}



