import { Component, OnInit } from '@angular/core';
import { Beneficio } from '../../../../interfaces/beneficios';
import { ActivatedRoute } from '@angular/router';
import { BeneficiosService } from "../../../../servicios/beneficios.service";


@Component({
  selector: 'app-detalles-beneficios',
  templateUrl: './detalles-beneficios.component.html',
  styleUrls: ['./detalles-beneficios.component.css']
})
export class DetallesBeneficiosComponent implements OnInit {

  public beneficioSeleccionado:Beneficio

  constructor(private route:ActivatedRoute, private beneficiosService:BeneficiosService) {
    this.beneficioSeleccionado = new Beneficio();
    
    
  }

  ngOnInit() {
    this.beneficiosService.PedirBeneficios().subscribe(
      beneficios_descargados => {
        this.route.params.subscribe(
          params => {
            let id_beneficio = params['id'];
            (beneficios_descargados as Beneficio[]).forEach(beneficio => {
              if(beneficio.id == id_beneficio){
                beneficio.detalles = JSON.parse(beneficio.detalles)
                this.beneficioSeleccionado = beneficio
              }
            });
          }
        )
      }
    )
  }

}



