import { Injectable } from '@angular/core';
import { RequestService } from "./request.service";
import { Beneficio } from "../interfaces/beneficios";

@Injectable()
export class BeneficiosService {

  public beneficios:Beneficio[];

  constructor(private req:RequestService ) { 
    this.beneficios = new Array<Beneficio>();
  }

  ObtenerBeneficios(){
    this.PedirBeneficios().subscribe(
      beneficios_recibidos => {
        this.beneficios = beneficios_recibidos as Beneficio[]
      },
      error => {
        console.log("Error al obtener beneficios");
      }
    )
  }

  PedirBeneficios(){
    return this.req.get('beneficios.listar.php');
  }

}



