import { Component, Output,EventEmitter } from '@angular/core';
import { MensajeError } from '../../../../../interfaces/mensaje';

@Component({
  selector: 'app-verificar-deudas',
  templateUrl: './verificar-deudas.component.html',
  styleUrls: ['./verificar-deudas.component.css']
})
export class VerificarDeudasComponent {
  terminos_y_condiciones = false;
  mensaje_error:MensajeError[] = new Array();

  @Output() aceptado:EventEmitter<boolean> = new EventEmitter();
  @Output() pasoAtras:EventEmitter<boolean> = new EventEmitter();
  @Output() errores:EventEmitter<Array<MensajeError>> = new EventEmitter();
  constructor() { }

  aceptar(){
    if(this.terminos_y_condiciones ==false){
      this.mensaje_error.push({severity:"error", summary:"Términos y condiciones", detail:"Debe leer y aceptar los términos y condiciones para continuar"})
    }
    
    if(this.mensaje_error.length > 0){
      this.errores.emit(this.mensaje_error);
      this.mensaje_error = []; 
    }else{
      this.aceptado.emit(this.terminos_y_condiciones);
    }
  }
}



